.header {
  border-bottom: 1px solid var(--border-color-2);

  .header-backdrop,
  .header-hamburger-btn,
  .header-close-btn {
    display: none;
  }

  .header-logo {
    a {
      font-size: 26px;
      text-transform: uppercase;
      font-weight: 700;
      color: var(--black-70);
    }

    span {
      color: var(--main-color);
    }
  }

  .menu-item {
    display: inline-block;
    margin-left: 40px;
    position: relative;

    a {
      display: block;
      padding: 24px 0;
      font-weight: 400;
      color: var(--black-90);
      text-transform: capitalize;
      @include transitionEase(color, 0.3s);

      i {
        font-size: 13px;
        margin-left: 3px;
        pointer-events: none;
        @include transitionEase(transform, 0.3s);
      }
    }

    &:hover {
      a {
        color: var(--main-color);
      }
    }
  }

  .sub-menu {
    position: absolute;
    top: 100%;
    background-color: var(--white);
    left: 0;
    width: 210px;
    padding: 10px 0;
    border-radius: 5px;
    box-shadow: var(--shadow);
    @include transitionEase(all, 0.3s);
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transform: translateY(10px);

    .sub-menu-item a {
      display: block;
      color: var(--black-90);
      padding: 10px 20px;
      font-weight: 400;
      @include transitionEase(color, 0.3s);

      &:hover {
        color: var(--main-color);
      }
    }
  }
}
