@media (min-width: 992px) {
  .header {
    .menu-item:hover {
      .sub-menu {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}

@media (max-width: 1199px) {
  .container {
    max-width: 100%;
    --bs-gutter-x: 30px;
  }
}

@media (max-width: 991px) {
  .header {
    padding: 12px 0;

    .header-backdrop,
    .header-close-btn,
    .header-hamburger-btn {
      display: block;
      height: 34px;
      width: 34px;
      cursor: pointer;
      border: 0;
      background-color: transparent;

      span {
        display: block;
        height: 2px;
        width: 24px;
        background-color: var(--black-90);
        position: relative;
        margin: auto;

        &::before,
        &::after {
          content: '';
          position: absolute;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: var(--black-90);
        }

        &::before {
          top: -7px;
        }

        &::after {
          top: 7px;
        }
      }
    }

    .header-close-btn {
      height: 40px;
      width: 40px;
      background-color: var(--main-color);
      position: absolute;
      left: 15px;
      top: 15px;
      color: var(--WHITE);
      border: 0;
      border-radius: 5px;
    }

    .header-menu {
      // display: none;
      position: fixed;
      width: 300px;
      height: 100vh;
      top: 0;
      // right: 0;
      right: -300px;
      z-index: 1000;
      padding: 70px 0 50px;
      @include transitionEase(all, 0.5s);
      background-color: var(--white);
      box-shadow: var(--shadow);
      overflow-y: auto;
      visibility: hidden;

      &.open {
        right: 0;
        visibility: visible;
      }
    }

    .menu-item {
      display: block;
      margin: 0;

      & > a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 15px;
        border-bottom: 1px solid var(--border-color-1);
      }

      &:first-child > a {
        border-top: 1px solid var(--border-color-1);
      }

      &.active {
        a i {
          transform: rotate(-180deg);
        }
      }
    }

    .sub-menu {
      position: relative;
      width: 100%;
      opacity: 1;
      visibility: visible;
      box-shadow: none;
      transform: translateY(0);
      top: auto;
      padding: 0;
      border-radius: 0;
      max-height: 0;
      overflow: hidden;

      .sub-menu-item {
        a {
          padding: 12px 45px;
          border-bottom: 1px solid var(--border-color-1);
        }
      }
    }

    .header-backdrop {
      position: fixed;
      z-index: 999;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      @include transitionEase(all, 0.5s);
      background-color: var(--white);

      &.active {
        visibility: visible;
        opacity: 0.8;
      }
    }
  }

  .circular-img {
    max-width: 280px;

    img {
      max-width: 240px;
    }

    .circular-img-inner {
      .circular-img-circle {
        height: 280px;
        width: 280px;
      }
    }
  }

  .banner-section .banner-text h1 {
    font-size: 35px;
  }

  .section-title .sub-title {
    font-size: 30px;
  }

  .course-sidebar {
    margin-top: 30px;
  }

  .course-instructor .img-box img {
    width: 100px;
  }

  .course-instructor .details-wrapper {
    margin-bottom: 15px;
  }
}

@media (max-width: 767px) {
  .container {
    --bs-gutter-x: 15px;
  }

  .box {
    padding: 20px 15px;
  }

  .banner-section {
    .banner-text {
      h1 {
        font-size: 24px;
      }

      h2 {
        font-size: 18px;
      }
    }
  }

  .courses-item .title {
    font-size: 18px;
  }

  .form-title,
  .section-title .sub-title {
    font-size: 22px;
  }

  .bai-section {
    text-align: center;
  }

  .course-header h2 {
    font-size: 24px;
  }

  .course-curriculum .accordion-item .accordion-button {
    padding-right: 15px;

    span {
      position: relative;
      right: auto;
      font-size: 12px;
      width: 100%;
      margin-top: 8px;
    }
  }

  .course-curriculum ul li {
    padding: 0 0 0 20px;
    span {
      font-size: 12px;
      position: relative;
      display: block;
      margin-top: 5px;
    }
  }

  .rating-box {
    margin-bottom: 15px;
  }

  .rating-summary {
    margin-bottom: 15px;
  }
}

@media (max-width: 575px) {
  .header .header-logo {
    a {
      font-size: 22px;
    }
  }
  .circular-img {
    max-width: 230px;

    .circular-img-inner {
      .circular-img-circle {
        width: 230px;
        height: 230px;
      }

      img {
        max-width: 200px;
      }
    }
  }

  .footer-item {
    text-align: center;
  }

  .modal-dialog {
    margin: 8px 15px;
  }
}
