/* --------------------------
BANNER SECTION
-----------------------------*/
.banner-section {
  padding: 80px 0;
  min-height: 680px;

  .circular-img-circle {
    background-color: var(--red-light);
  }

  .banner-text {
    h2 {
      font-size: 20px;
      color: var(--main-color);
    }

    h1 {
      font-size: 45px;
      font-weight: 700;
    }
  }
}

/* --------------------------
FUN FACTS SECTION
-----------------------------*/
.fun-facts-item {
  padding: 15px 0;

  h2 {
    font-weight: 700;
    font-size: 30px;
    text-transform: uppercase;
  }

  p {
    margin: 0;
    text-transform: uppercase;
  }

  .style-1 {
    color: var(--black-70);
  }
  .style-2 {
    color: var(--green-light);
  }
  .style-3 {
    color: var(--red-light);
  }
  .style-4 {
    color: var(--orange-light);
  }
}

/* --------------------------
COURSES SECTION
-----------------------------*/
.courses-item {
  margin-bottom: 30px;
  position: relative;

  .img-box {
    img {
      width: 100%;
      border-radius: 5px;
    }
  }

  .link {
    color: var(--black-70);
    display: inline-block;
  }

  .title {
    text-transform: capitalize;
    margin: 15px 0;
    @include transitionEase(color, 0.3s);

    &:hover {
      color: var(--main-color);
    }
  }

  .instructor {
    text-transform: capitalize;
    margin-bottom: 12px;
    img {
      width: 30px;
      border-radius: 50%;
      margin-right: 5px;
    }
  }

  .average-rating {
    font-weight: 600;
    color: var(--orange);
  }

  .average-stars {
    i {
      color: var(--orange);
    }
  }

  .rating {
    font-size: 14px;
  }

  .price {
    position: absolute;
    top: 15px;
    right: 15px;
    background-color: var(--main-color);
    color: var(--WHITE);
    font-weight: 600;
    padding: 4px 8px;
    border-radius: 4px;
  }
}

/* --------------------------
TESTIMONIALS SECTION
-----------------------------*/
.testimonials-section {
  .img-box {
    height: 150px;
    width: 150px;
    background-color: var(--red-light);
    margin: 30px auto 50px;

    &::before,
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      border: 1px solid var(--red-light);
      animation: spin 15s linear infinite;
    }

    &::before {
      height: 180px;
      width: 180px;
      left: -15px;
      top: -15px;
      border-left: 1px solid transparent;
    }

    &::after {
      height: 210px;
      width: 210px;
      left: -30px;
      top: -30px;
      border-right: 1px solid transparent;
    }
  }

  .carousel-control-prev,
  .carousel-control-next {
    position: relative;
    height: 35px;
    width: 35px;
    background-color: var(--main-color);
    display: inline-block;
    border-radius: 50%;
    margin: 0 4px;
  }

  .testimonials-item {
    h3 {
      text-transform: capitalize;
    }
    .text-2 {
      text-transform: capitalize;
      margin: 0;
    }
  }
}

/* --------------------------
BAI SECTION
-----------------------------*/

.circular-img-circle {
  background-color: var(--yellow-light);
}

/* --------------------------
COURSE-DETAILS SECTION
-----------------------------*/
// COURSE HEADER
.course-header {
  h2 {
    font-size: 30px;
    font-weight: 600;
    margin: 0 0 15px;
  }

  .average-rating {
    font-weight: 600;
    color: var(--orange);
  }

  .average-stars i {
    font-size: 14px;
    color: var(--orange);
  }

  .rating span {
    vertical-align: middle;
  }

  ul li {
    margin: 6px 0 0;
    text-transform: capitalize;

    span {
      margin-left: 5px;
    }
  }
}

// COURSE TABS
.course-tabs {
  margin: 30px 0 20px;

  .nav-link {
    margin: 0 10px;
  }
}

// COURSE CURRICULUM
.course-curriculum {
  .accordion-item {
    border-color: var(--border-color-1);
    background-color: transparent;

    .accordion-button {
      background-color: transparent;
      color: var(--black-90);
      font-weight: 300;
      flex-wrap: wrap;
      padding-left: 50px;
      padding-right: 160px;
      line-height: 1.5;

      span {
        position: absolute;
        right: 17px;
        font-size: 14px;
      }

      &::after {
        content: '\f077';
        font-family: 'Font Awesome 5 Free';
        font-weight: 900;
        background-image: none;
        position: absolute;
        left: 17px;
        top: 50%;
        transform: translateY(-50%);
        height: auto;
        width: auto;
        line-height: 1;
      }

      &:focus {
        box-shadow: none;
      }

      &:not(.collapsed) {
        font-weight: 600;
        border-bottom: 1px solid var(--border-color-1);
        transition: none;
        box-shadow: none;

        span {
          font-weight: 500;
        }

        &::after {
          transform: translateY(-50%) rotate(-180deg);
        }
      }
    }
  }

  ul li {
    position: relative;
    padding: 0 50px 0 20px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    span {
      position: absolute;
      font-size: 14px;
      right: 0;
      top: 0;
    }

    i {
      position: absolute;
      left: 0;
      top: 6px;
      font-size: 12px;
      color: var(--black-70);
    }
  }
}

// COURSE DESCRIPTION
.course-description {
  h4 {
    font-size: 16px;
  }
}

// COURSE INSTRUCTOR
.course-instructor {
  .img-box {
    img {
      max-width: 128px;
      margin-bottom: 15px;
    }
  }

  h4 {
    font-size: 16px;
    text-transform: capitalize;
    margin-bottom: 10px;

    span {
      font-weight: 300;
    }
  }

  ul li {
    i {
      color: var(--main-color);
      margin-right: 5px;
    }

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}

// COURSE REVIEWS

.rating-summary {
  .average-rating {
    font-size: 40px;
    font-weight: 700;
    color: var(--black-90);
  }

  .average-stars {
    i {
      color: var(--orange);
    }
  }

  .rating-bars-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    .star-text {
      min-width: 60px;
      color: var(--orange);
    }
  }

  .progress {
    height: 8px;
    width: calc(100% - 120px);
    background-color: var(--border-color-2);
    border-radius: 0;
    margin-right: 20px;

    .progress-bar {
      background-color: var(--main-color);
    }
  }
}

.reviews-filter {
  margin-bottom: 35px;
  width: 200px;
}

.reviews-item {
  position: relative;
  margin-bottom: 30px;
  padding-left: 65px;

  .img-box {
    max-width: 50px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
  }

  h4 {
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
    margin-bottom: 5px;
    color: var(--black-90);
  }

  .stars-rating {
    i {
      font-size: 14px;
      color: var(--orange);
    }

    .date {
      font-size: 14px;
      margin-left: 5px;
      display: inline-block;
    }
  }

  p {
    margin-top: 5px;
  }
}

/* --------------------------
CONTACT SECTION
-----------------------------*/
.contact-section {
  .contact-item {
    position: relative;
    padding-left: 55px;
    margin-bottom: 40px;

    .icon-box {
      position: absolute;
      height: 40px;
      width: 40px;
      background-color: var(--main-color);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      left: 0;
      top: 0;
      color: var(--WHITE);
    }
  }
}
