@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700;800;900&display=swap");
:root {
  --orange: hsl(36, 80%, 50%);
  --white: hsl(0, 0%, 100%);
  --WHITE: hsl(0, 0%, 100%);
  --BLACK: hsl(0, 0%, 0%);
  --black-90: hsl(0, 0%, 10%);
  --black-70: hsl(0, 0%, 30%);
  --black-alpha-40: hsla(0, 0%, 0%, 0.4);
  --yellow-light: hsl(44, 95%, 83%);
  --green-light: hsl(158, 68%, 75%);
  --red-light: hsl(0, 84%, 80%);
  --orange-light: hsl(22, 85%, 80%);
  --select-box-bg-color: hsl(0, 0%, 100%);
  --border-color-1: hsl(0, 0%, 90%);
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--body-bg-color);
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: var(--black-70);
  min-height: 100vh;
  overflow-x: hidden;
}

::selection {
  color: var(--WHITE);
  background-color: var(--main-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--black-90);
  line-height: 1.3;
}

h3 {
  font-size: 20px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--main-color);
}
a:hover {
  color: var(--main-color);
}

img {
  max-width: 100%;
}

.section-padding {
  padding: 60px 0;
}

.section-title {
  margin-bottom: 40px;
}
.section-title .title {
  font-size: 20px;
  color: var(--main-color);
  text-transform: capitalize;
}
.section-title .sub-title {
  font-size: 35px;
  font-weight: 600;
  color: var(--black-90);
}

/* --------------------------
CUSTOMIZED BOOTSTRAP CLASSES
-----------------------------*/
.container {
  --bs-gutter-x: 15px;
  max-width: 1140px;
}

.row {
  --bs-gutter-x: 30px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes bubble {
  0% {
    transform: translateY(0vh);
    opacity: 0;
  }
  5%, 95% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh);
    opacity: 0;
  }
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  25%, 50% {
    transform: rotate(180deg);
  }
  75%, 100% {
    transform: rotate(360deg);
  }
}
@keyframes loaderIn {
  0%, 25%, 100% {
    height: 0%;
  }
  50%, 75% {
    height: 100%;
  }
}
body.t-dark {
  --body-bg-color: hsl(240, 10%, 19%);
  --black-70: hsl(0, 0%, 87%);
  --black-90: hsl(0, 0%, 100%);
  --shadow: 0 0 10px hsla(0, 0%, 0% 0.08);
  --white: hsl(240, 8%, 21%);
  --select-box-bg-color: hsl(240, 8%, 21%);
  --border-color-2: hsl(240, 9%, 25%);
  --border-color-1: hsl(240, 9%, 30%);
}

/* --------------------------
BANNER SECTION
-----------------------------*/
.banner-section {
  padding: 80px 0;
  min-height: 680px;
}
.banner-section .circular-img-circle {
  background-color: var(--red-light);
}
.banner-section .banner-text h2 {
  font-size: 20px;
  color: var(--main-color);
}
.banner-section .banner-text h1 {
  font-size: 45px;
  font-weight: 700;
}

/* --------------------------
FUN FACTS SECTION
-----------------------------*/
.fun-facts-item {
  padding: 15px 0;
}
.fun-facts-item h2 {
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
}
.fun-facts-item p {
  margin: 0;
  text-transform: uppercase;
}
.fun-facts-item .style-1 {
  color: var(--black-70);
}
.fun-facts-item .style-2 {
  color: var(--green-light);
}
.fun-facts-item .style-3 {
  color: var(--red-light);
}
.fun-facts-item .style-4 {
  color: var(--orange-light);
}

/* --------------------------
COURSES SECTION
-----------------------------*/
.courses-item {
  margin-bottom: 30px;
  position: relative;
}
.courses-item .img-box img {
  width: 100%;
  border-radius: 5px;
}
.courses-item .link {
  color: var(--black-70);
  display: inline-block;
}
.courses-item .title {
  text-transform: capitalize;
  margin: 15px 0;
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.courses-item .title:hover {
  color: var(--main-color);
}
.courses-item .instructor {
  text-transform: capitalize;
  margin-bottom: 12px;
}
.courses-item .instructor img {
  width: 30px;
  border-radius: 50%;
  margin-right: 5px;
}
.courses-item .average-rating {
  font-weight: 600;
  color: var(--orange);
}
.courses-item .average-stars i {
  color: var(--orange);
}
.courses-item .rating {
  font-size: 14px;
}
.courses-item .price {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: var(--main-color);
  color: var(--WHITE);
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
}

/* --------------------------
TESTIMONIALS SECTION
-----------------------------*/
.testimonials-section .img-box {
  height: 150px;
  width: 150px;
  background-color: var(--red-light);
  margin: 30px auto 50px;
}
.testimonials-section .img-box::before, .testimonials-section .img-box::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 1px solid var(--red-light);
  animation: spin 15s linear infinite;
}
.testimonials-section .img-box::before {
  height: 180px;
  width: 180px;
  left: -15px;
  top: -15px;
  border-left: 1px solid transparent;
}
.testimonials-section .img-box::after {
  height: 210px;
  width: 210px;
  left: -30px;
  top: -30px;
  border-right: 1px solid transparent;
}
.testimonials-section .carousel-control-prev,
.testimonials-section .carousel-control-next {
  position: relative;
  height: 35px;
  width: 35px;
  background-color: var(--main-color);
  display: inline-block;
  border-radius: 50%;
  margin: 0 4px;
}
.testimonials-section .testimonials-item h3 {
  text-transform: capitalize;
}
.testimonials-section .testimonials-item .text-2 {
  text-transform: capitalize;
  margin: 0;
}

/* --------------------------
BAI SECTION
-----------------------------*/
.circular-img-circle {
  background-color: var(--yellow-light);
}

/* --------------------------
COURSE-DETAILS SECTION
-----------------------------*/
.course-header h2 {
  font-size: 30px;
  font-weight: 600;
  margin: 0 0 15px;
}
.course-header .average-rating {
  font-weight: 600;
  color: var(--orange);
}
.course-header .average-stars i {
  font-size: 14px;
  color: var(--orange);
}
.course-header .rating span {
  vertical-align: middle;
}
.course-header ul li {
  margin: 6px 0 0;
  text-transform: capitalize;
}
.course-header ul li span {
  margin-left: 5px;
}

.course-tabs {
  margin: 30px 0 20px;
}
.course-tabs .nav-link {
  margin: 0 10px;
}

.course-curriculum .accordion-item {
  border-color: var(--border-color-1);
  background-color: transparent;
}
.course-curriculum .accordion-item .accordion-button {
  background-color: transparent;
  color: var(--black-90);
  font-weight: 300;
  flex-wrap: wrap;
  padding-left: 50px;
  padding-right: 160px;
  line-height: 1.5;
}
.course-curriculum .accordion-item .accordion-button span {
  position: absolute;
  right: 17px;
  font-size: 14px;
}
.course-curriculum .accordion-item .accordion-button::after {
  content: "\f077";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  background-image: none;
  position: absolute;
  left: 17px;
  top: 50%;
  transform: translateY(-50%);
  height: auto;
  width: auto;
  line-height: 1;
}
.course-curriculum .accordion-item .accordion-button:focus {
  box-shadow: none;
}
.course-curriculum .accordion-item .accordion-button:not(.collapsed) {
  font-weight: 600;
  border-bottom: 1px solid var(--border-color-1);
  transition: none;
  box-shadow: none;
}
.course-curriculum .accordion-item .accordion-button:not(.collapsed) span {
  font-weight: 500;
}
.course-curriculum .accordion-item .accordion-button:not(.collapsed)::after {
  transform: translateY(-50%) rotate(-180deg);
}
.course-curriculum ul li {
  position: relative;
  padding: 0 50px 0 20px;
}
.course-curriculum ul li:not(:last-child) {
  margin-bottom: 10px;
}
.course-curriculum ul li span {
  position: absolute;
  font-size: 14px;
  right: 0;
  top: 0;
}
.course-curriculum ul li i {
  position: absolute;
  left: 0;
  top: 6px;
  font-size: 12px;
  color: var(--black-70);
}

.course-description h4 {
  font-size: 16px;
}

.course-instructor .img-box img {
  max-width: 128px;
  margin-bottom: 15px;
}
.course-instructor h4 {
  font-size: 16px;
  text-transform: capitalize;
  margin-bottom: 10px;
}
.course-instructor h4 span {
  font-weight: 300;
}
.course-instructor ul li i {
  color: var(--main-color);
  margin-right: 5px;
}
.course-instructor ul li:not(:last-child) {
  margin-bottom: 5px;
}

.rating-summary .average-rating {
  font-size: 40px;
  font-weight: 700;
  color: var(--black-90);
}
.rating-summary .average-stars i {
  color: var(--orange);
}
.rating-summary .rating-bars-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.rating-summary .rating-bars-item .star-text {
  min-width: 60px;
  color: var(--orange);
}
.rating-summary .progress {
  height: 8px;
  width: calc(100% - 120px);
  background-color: var(--border-color-2);
  border-radius: 0;
  margin-right: 20px;
}
.rating-summary .progress .progress-bar {
  background-color: var(--main-color);
}

.reviews-filter {
  margin-bottom: 35px;
  width: 200px;
}

.reviews-item {
  position: relative;
  margin-bottom: 30px;
  padding-left: 65px;
}
.reviews-item .img-box {
  max-width: 50px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
}
.reviews-item h4 {
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 5px;
  color: var(--black-90);
}
.reviews-item .stars-rating i {
  font-size: 14px;
  color: var(--orange);
}
.reviews-item .stars-rating .date {
  font-size: 14px;
  margin-left: 5px;
  display: inline-block;
}
.reviews-item p {
  margin-top: 5px;
}

/* --------------------------
CONTACT SECTION
-----------------------------*/
.contact-section .contact-item {
  position: relative;
  padding-left: 55px;
  margin-bottom: 40px;
}
.contact-section .contact-item .icon-box {
  position: absolute;
  height: 40px;
  width: 40px;
  background-color: var(--main-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0;
  top: 0;
  color: var(--WHITE);
}

.circular-img {
  max-width: 360px;
  margin: auto;
  position: relative;
  z-index: 1;
}
.circular-img .circular-img-inner {
  text-align: center;
  border-radius: 0 0 180px 180px;
  overflow: hidden;
}
.circular-img .circular-img-inner .circular-img-circle {
  height: 360px;
  width: 360px;
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius: 50%;
}
.circular-img img {
  position: relative;
  z-index: 1;
}

.btn-theme {
  background-color: var(--main-color);
  color: var(--WHITE);
  font-size: 16px;
  text-transform: capitalize;
  font-weight: 500;
  padding: 8px 20px;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.btn-theme:hover {
  color: var(--WHITE);
  background-color: var(--button-hover-color);
}
.btn-theme:focus {
  box-shadow: 0 0 8px var(--main-color);
}

.btn-block {
  width: 100%;
}

.btn-form {
  height: 48px;
}

.box {
  background-color: var(--white);
  border-radius: 5px;
  padding: 30px;
  box-shadow: var(--shadow);
}

.decoration-circles-item {
  position: absolute;
  border-radius: 50%;
  opacity: 0.4;
}
.decoration-circles-item:nth-child(1) {
  height: 30px;
  width: 30px;
  background-color: var(--yellow-light);
  left: 10%;
  top: 20%;
}
.decoration-circles-item:nth-child(2) {
  height: 50px;
  width: 50px;
  background-color: var(--green-light);
  left: 40%;
  top: 40%;
}
.decoration-circles-item:nth-child(3) {
  height: 30px;
  width: 30px;
  background-color: var(--red-light);
  left: 70%;
  top: 70%;
}
.decoration-circles-item:nth-child(4) {
  height: 20px;
  width: 20px;
  background-color: var(--red-light);
  left: 20%;
  top: 50%;
}

.decoration-imgs-item {
  position: absolute;
  border-radius: 50%;
  opacity: 0.4;
}
.decoration-imgs-item:nth-child(1) {
  height: 60px;
  width: 60px;
  background-color: var(--yellow-light);
  left: 20%;
  top: 30%;
}
.decoration-imgs-item:nth-child(2) {
  height: 80px;
  width: 80px;
  background-color: var(--red-light);
  left: 90%;
  top: 40%;
}
.decoration-imgs-item:nth-child(3) {
  height: 40px;
  width: 40px;
  background-color: var(--green-light);
  left: 10%;
  top: 60%;
}

.breadcrumb-nav {
  padding: 10px 0;
}
.breadcrumb-nav .breadcrumb-item {
  font-size: 14px;
  text-transform: capitalize;
}
.breadcrumb-nav .breadcrumb-item::before, .breadcrumb-nav .breadcrumb-item.active {
  color: var(--black-70);
}

.nav-tabs .nav-link {
  background-color: var(--white);
  text-transform: capitalize;
  border-color: var(--border-color-2);
  border-radius: 5px;
  padding: 5px 12px;
  margin: 0 5px 10px;
  color: var(--black-70);
  font-size: 16px;
}
.nav-tabs .nav-link.active {
  border-color: transparent;
  background-color: var(--main-color);
  color: var(--WHITE);
}
.nav-tabs .nav-link:hover {
  border-color: var(--border-color-2);
}

.page-link,
.page-item.disabled .page-link {
  border: none;
  outline: none;
  background-color: transparent;
  color: var(--black-70);
}

.page-item.active .page-link {
  background-color: var(--main-color);
}

.page-link:hover {
  background-color: var(--main-color);
  color: var(--WHITE);
}
.page-link:focus {
  box-shadow: none;
  background-color: var(--red-light);
  color: var(--WHITE);
}
.page-link:active {
  outline: none;
  border: none;
}

.video-modal .btn-close {
  height: 35px;
  width: 35px;
  background-image: none;
  border-radius: 50%;
  opacity: 1;
  z-index: 1;
  position: absolute;
  right: 0;
  top: -35px;
  color: var(--WHITE);
  font-size: 16px;
  padding: 0;
}
.video-modal .modal-content {
  border: 0;
}

.style-switcher {
  position: fixed;
  width: 250px;
  height: 100%;
  background-color: var(--white);
  right: -250px;
  top: 0;
  z-index: 10;
  padding: 30px;
  box-shadow: var(--shadow);
  transition-property: right;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.style-switcher.open {
  right: 0;
}
.style-switcher .style-switcher-toggler {
  height: 40px;
  width: 40px;
  position: absolute;
  background-color: var(--main-color);
  color: var(--WHITE);
  top: 28%;
  left: -40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px 0 0 8px;
  cursor: pointer;
}
.style-switcher h3 {
  font-size: 18px;
  text-transform: capitalize;
  border-bottom: 1px dashed var(--border-color-1);
  padding: 0 0 10px;
}
.style-switcher .style-switcher-item {
  padding: 10px 0;
  border-bottom: 1px dashed var(--border-color-1);
}
.style-switcher .style-switcher-item .theme-colors button {
  height: 30px;
  width: 30px;
  border: none;
  border-radius: 50%;
  vertical-align: middle;
  padding: 0;
  position: relative;
}
.style-switcher .style-switcher-item .theme-colors button::before {
  content: "\f00c";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 12px;
  color: var(--WHITE);
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.style-switcher .style-switcher-item .theme-colors button.active::before {
  opacity: 1;
}
.style-switcher .style-switcher-item .theme-colors .color-1 {
  background-color: #e55252;
}
.style-switcher .style-switcher-item .theme-colors .color-2 {
  background-color: #526ae5;
}
.style-switcher .style-switcher-item .theme-colors .color-3 {
  background-color: #e5aa52;
}
.style-switcher .style-switcher-item .theme-colors .color-4 {
  background-color: #ad52e5;
}
.style-switcher .style-switcher-item .theme-colors .color-5 {
  background-color: #52e5be;
}

.bubble-animation-item {
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 2px solid transparent;
  z-index: -1;
}
.bubble-animation-item:nth-child(1) {
  border-color: var(--yellow-light);
  top: 120%;
  left: 10%;
  animation: bubble 30s linear infinite;
}
.bubble-animation-item:nth-child(2) {
  border-color: var(--green-light);
  top: 60%;
  left: 50%;
  animation: bubble 40s linear infinite;
}
.bubble-animation-item:nth-child(3) {
  border-color: var(--red-light);
  top: 90%;
  left: 90%;
  animation: bubble 50s linear infinite;
}
.bubble-animation-item:nth-child(4) {
  border-color: var(--yellow-light);
  top: 80%;
  left: 60%;
  animation: bubble 60s linear infinite;
}
.bubble-animation-item:nth-child(5) {
  border-color: var(--green-light);
  top: 30%;
  left: 10%;
  animation: bubble 70s linear infinite;
}
.bubble-animation-item:nth-child(6) {
  border-color: var(--red-light);
  top: 20%;
  left: 30%;
  animation: bubble 80s linear infinite;
}
.bubble-animation-item:nth-child(7) {
  border-color: var(--yellow-light);
  top: 100%;
  left: 5%;
  animation: bubble 60s linear infinite;
}
.bubble-animation-item:nth-child(8) {
  border-color: var(--green-light);
  top: 110%;
  left: 33%;
  animation: bubble 70s linear infinite;
}
.bubble-animation-item:nth-child(9) {
  border-color: var(--red-light);
  top: 120%;
  left: 65%;
  animation: bubble 80s linear infinite;
}

.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1001;
  background-color: var(--white);
  display: flex;
}
.page-loader.fade-out {
  opacity: 0;
  transition-property: all;
  transition-duration: 0.6s;
  transition-timing-function: ease;
}
.page-loader div {
  margin: auto;
  height: 32px;
  width: 32px;
  border: 4px solid var(--main-color);
  position: relative;
  animation: loader 2s linear infinite;
}
.page-loader div::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: var(--main-color);
  left: 0;
  top: 0;
  animation: loaderIn 2s linear infinite;
}

.header {
  border-bottom: 1px solid var(--border-color-2);
}
.header .header-backdrop,
.header .header-hamburger-btn,
.header .header-close-btn {
  display: none;
}
.header .header-logo a {
  font-size: 26px;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--black-70);
}
.header .header-logo span {
  color: var(--main-color);
}
.header .menu-item {
  display: inline-block;
  margin-left: 40px;
  position: relative;
}
.header .menu-item a {
  display: block;
  padding: 24px 0;
  font-weight: 400;
  color: var(--black-90);
  text-transform: capitalize;
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header .menu-item a i {
  font-size: 13px;
  margin-left: 3px;
  pointer-events: none;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header .menu-item:hover a {
  color: var(--main-color);
}
.header .sub-menu {
  position: absolute;
  top: 100%;
  background-color: var(--white);
  left: 0;
  width: 210px;
  padding: 10px 0;
  border-radius: 5px;
  box-shadow: var(--shadow);
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transform: translateY(10px);
}
.header .sub-menu .sub-menu-item a {
  display: block;
  color: var(--black-90);
  padding: 10px 20px;
  font-weight: 400;
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header .sub-menu .sub-menu-item a:hover {
  color: var(--main-color);
}

.footer-top {
  padding: 50px 0 20px;
  border-top: 1px solid var(--border-color-2);
}
.footer-top .footer-item {
  margin: 0 0 30px;
}
.footer-top .footer-item h3 {
  text-transform: capitalize;
  margin: 0 0 20px;
}
.footer-top .footer-item .footer-logo {
  text-transform: uppercase;
  color: var(--black-70);
}
.footer-top .footer-item .footer-logo span {
  color: var(--main-color);
}
.footer-top .footer-item ul li:not(:last-child) {
  margin-bottom: 8px;
}
.footer-top .footer-item ul li a {
  text-transform: capitalize;
  color: var(--black-70);
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  position: relative;
}
.footer-top .footer-item ul li a:hover {
  color: var(--main-color);
}
.footer-top .footer-item ul li a::before {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  background-color: var(--main-color);
  left: 0;
  bottom: 0;
  transition-property: width;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.footer-top .footer-item ul li a:hover::before {
  width: 100%;
}
.footer-top .footer-item ul li a .social-icon {
  margin-right: 5px;
}

.footer-bottom {
  border-top: 1px solid var(--border-color-2);
}
.footer-bottom p {
  font-size: 14px;
}

.form-group {
  margin-bottom: 25px;
  position: relative;
}

.form-control {
  height: 48px;
  border-color: var(--border-color-1);
  color: var(--black-90);
  background-color: transparent;
}
.form-control:focus {
  background-color: transparent;
  border-color: var(--border-color-1);
  color: var(--black-90);
  box-shadow: none;
}

.select-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  font-size: 13px;
}

select.form-control:focus option {
  background-color: var(--select-box-bg-color);
}

.form-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 30px;
}

.form-control::placeholder {
  color: var(--black-70);
}

textarea.form-control {
  height: 120px;
  resize: none;
}

.course-sidebar .img-box {
  margin-bottom: 20px;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
}
.course-sidebar .img-box::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: var(--black-alpha-40);
}
.course-sidebar .img-box .play-icon {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 50%;
  top: 50%;
  background-color: var(--main-color);
  transform: translate(-50%, -50%);
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: var(--WHITE);
}
.course-sidebar .img-box p {
  position: absolute;
  color: var(--WHITE);
  font-weight: 500;
  top: calc(50% + 40px);
  width: 100%;
}
.course-sidebar .price span {
  margin-right: 8px;
}
.course-sidebar .price .price-old {
  text-decoration: line-through;
}
.course-sidebar .price .price-new {
  font-size: 30px;
  font-weight: 700;
  color: var(--black-90);
}
.course-sidebar .price .price-discount {
  color: var(--main-color);
}
.course-sidebar .features-list {
  margin-bottom: 20px;
}
.course-sidebar .features-list li {
  position: relative;
  padding-left: 21px;
  margin-bottom: 8px;
}
.course-sidebar .features-list li::before {
  content: "";
  position: absolute;
  height: 6px;
  width: 6px;
  background-color: var(--main-color);
  left: 0;
  top: 9px;
  border-radius: 50%;
}

@media (min-width: 992px) {
  .header .menu-item:hover .sub-menu {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 1199px) {
  .container {
    max-width: 100%;
    --bs-gutter-x: 30px;
  }
}
@media (max-width: 991px) {
  .header {
    padding: 12px 0;
  }
  .header .header-backdrop,
.header .header-close-btn,
.header .header-hamburger-btn {
    display: block;
    height: 34px;
    width: 34px;
    cursor: pointer;
    border: 0;
    background-color: transparent;
  }
  .header .header-backdrop span,
.header .header-close-btn span,
.header .header-hamburger-btn span {
    display: block;
    height: 2px;
    width: 24px;
    background-color: var(--black-90);
    position: relative;
    margin: auto;
  }
  .header .header-backdrop span::before, .header .header-backdrop span::after,
.header .header-close-btn span::before,
.header .header-close-btn span::after,
.header .header-hamburger-btn span::before,
.header .header-hamburger-btn span::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--black-90);
  }
  .header .header-backdrop span::before,
.header .header-close-btn span::before,
.header .header-hamburger-btn span::before {
    top: -7px;
  }
  .header .header-backdrop span::after,
.header .header-close-btn span::after,
.header .header-hamburger-btn span::after {
    top: 7px;
  }
  .header .header-close-btn {
    height: 40px;
    width: 40px;
    background-color: var(--main-color);
    position: absolute;
    left: 15px;
    top: 15px;
    color: var(--WHITE);
    border: 0;
    border-radius: 5px;
  }
  .header .header-menu {
    position: fixed;
    width: 300px;
    height: 100vh;
    top: 0;
    right: -300px;
    z-index: 1000;
    padding: 70px 0 50px;
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: ease;
    background-color: var(--white);
    box-shadow: var(--shadow);
    overflow-y: auto;
    visibility: hidden;
  }
  .header .header-menu.open {
    right: 0;
    visibility: visible;
  }
  .header .menu-item {
    display: block;
    margin: 0;
  }
  .header .menu-item > a {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 15px;
    border-bottom: 1px solid var(--border-color-1);
  }
  .header .menu-item:first-child > a {
    border-top: 1px solid var(--border-color-1);
  }
  .header .menu-item.active a i {
    transform: rotate(-180deg);
  }
  .header .sub-menu {
    position: relative;
    width: 100%;
    opacity: 1;
    visibility: visible;
    box-shadow: none;
    transform: translateY(0);
    top: auto;
    padding: 0;
    border-radius: 0;
    max-height: 0;
    overflow: hidden;
  }
  .header .sub-menu .sub-menu-item a {
    padding: 12px 45px;
    border-bottom: 1px solid var(--border-color-1);
  }
  .header .header-backdrop {
    position: fixed;
    z-index: 999;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: ease;
    background-color: var(--white);
  }
  .header .header-backdrop.active {
    visibility: visible;
    opacity: 0.8;
  }

  .circular-img {
    max-width: 280px;
  }
  .circular-img img {
    max-width: 240px;
  }
  .circular-img .circular-img-inner .circular-img-circle {
    height: 280px;
    width: 280px;
  }

  .banner-section .banner-text h1 {
    font-size: 35px;
  }

  .section-title .sub-title {
    font-size: 30px;
  }

  .course-sidebar {
    margin-top: 30px;
  }

  .course-instructor .img-box img {
    width: 100px;
  }

  .course-instructor .details-wrapper {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .container {
    --bs-gutter-x: 15px;
  }

  .box {
    padding: 20px 15px;
  }

  .banner-section .banner-text h1 {
    font-size: 24px;
  }
  .banner-section .banner-text h2 {
    font-size: 18px;
  }

  .courses-item .title {
    font-size: 18px;
  }

  .form-title,
.section-title .sub-title {
    font-size: 22px;
  }

  .bai-section {
    text-align: center;
  }

  .course-header h2 {
    font-size: 24px;
  }

  .course-curriculum .accordion-item .accordion-button {
    padding-right: 15px;
  }
  .course-curriculum .accordion-item .accordion-button span {
    position: relative;
    right: auto;
    font-size: 12px;
    width: 100%;
    margin-top: 8px;
  }

  .course-curriculum ul li {
    padding: 0 0 0 20px;
  }
  .course-curriculum ul li span {
    font-size: 12px;
    position: relative;
    display: block;
    margin-top: 5px;
  }

  .rating-box {
    margin-bottom: 15px;
  }

  .rating-summary {
    margin-bottom: 15px;
  }
}
@media (max-width: 575px) {
  .header .header-logo a {
    font-size: 22px;
  }

  .circular-img {
    max-width: 230px;
  }
  .circular-img .circular-img-inner .circular-img-circle {
    width: 230px;
    height: 230px;
  }
  .circular-img .circular-img-inner img {
    max-width: 200px;
  }

  .footer-item {
    text-align: center;
  }

  .modal-dialog {
    margin: 8px 15px;
  }
}