.btn-theme {
  background-color: var(--main-color);
  color: var(--WHITE);
  font-size: 16px;
  text-transform: capitalize;
  font-weight: 500;
  padding: 8px 20px;
  @include transitionEase(all, 0.3s);

  &:hover {
    color: var(--WHITE);
    background-color: var(--button-hover-color);
  }

  &:focus {
    box-shadow: 0 0 8px var(--main-color);
  }
}

.btn-block {
  width: 100%;
}

.btn-form {
  height: 48px;
}
