.style-switcher {
  position: fixed;
  width: 250px;
  height: 100%;
  background-color: var(--white);
  // right: 0;
  right: -250px;
  top: 0;
  z-index: 10;
  padding: 30px;
  box-shadow: var(--shadow);
  @include transitionEase(right, 0.3s);

  &.open {
    right: 0;
  }

  .style-switcher-toggler {
    height: 40px;
    width: 40px;
    position: absolute;
    background-color: var(--main-color);
    color: var(--WHITE);
    top: 28%;
    left: -40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px 0 0 8px;
    cursor: pointer;
  }

  h3 {
    font-size: 18px;
    text-transform: capitalize;
    border-bottom: 1px dashed var(--border-color-1);
    padding: 0 0 10px;
  }

  .style-switcher-item {
    padding: 10px 0;
    border-bottom: 1px dashed var(--border-color-1);

    .theme-colors {
      button {
        height: 30px;
        width: 30px;
        border: none;
        border-radius: 50%;
        vertical-align: middle;
        padding: 0;
        position: relative;

        &::before {
          content: '\f00c';
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          font-size: 12px;
          color: var(--WHITE);
          @include transitionEase(all, 0.3s);
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          opacity: 0;
        }

        &.active::before {
          opacity: 1;
        }
      }

      .color-1 {
        background-color: hsl(0, 74%, 61%);
      }

      .color-2 {
        background-color: hsl(230, 74%, 61%);
      }

      .color-3 {
        background-color: hsl(36, 74%, 61%);
      }

      .color-4 {
        background-color: hsl(277, 74%, 61%);
      }

      .color-5 {
        background-color: hsl(164, 74%, 61%);
      }
    }
  }
}
