.footer-top {
  padding: 50px 0 20px;
  border-top: 1px solid var(--border-color-2);

  .footer-item {
    margin: 0 0 30px;

    h3 {
      text-transform: capitalize;
      margin: 0 0 20px;
    }

    .footer-logo {
      text-transform: uppercase;
      color: var(--black-70);

      span {
        color: var(--main-color);
      }
    }

    ul li:not(:last-child) {
      margin-bottom: 8px;
    }

    ul li a {
      text-transform: capitalize;
      color: var(--black-70);
      @include transitionEase(color, 0.3s);
      position: relative;

      &:hover {
        color: var(--main-color);
      }

      &::before {
        content: '';
        position: absolute;
        width: 0%;
        height: 1px;
        background-color: var(--main-color);
        left: 0;
        bottom: 0;
        @include transitionEase(width, 0.3s);
      }

      &:hover::before {
        width: 100%;
      }

      .social-icon {
        margin-right: 5px;
      }
    }
  }
}

.footer-bottom {
  border-top: 1px solid var(--border-color-2);

  p {
    font-size: 14px;
  }
}
