// GLOBAL STYLE
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--body-bg-color);
  font-family: $main-font;
  font-size: 16px;
  font-weight: 300;
  // line-height: 1.6;
  color: var(--black-70);
  min-height: 100vh;
  overflow-x: hidden;
}

::selection {
  color: var(--WHITE);
  background-color: var(--main-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--black-90);
  line-height: 1.3;
}

h3 {
  font-size: 20px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--main-color);

  &:hover {
    color: var(--main-color);
  }
}

img {
  max-width: 100%;
}

.section-padding {
  padding: 60px 0;
}

.section-title {
  margin-bottom: 40px;

  .title {
    font-size: 20px;
    color: var(--main-color);
    text-transform: capitalize;
  }

  .sub-title {
    font-size: 35px;
    font-weight: 600;
    color: var(--black-90);
  }
}
