.circular-img {
  max-width: 360px;
  //   background-color: red;
  margin: auto;
  position: relative;
  z-index: 1;

  .circular-img-inner {
    text-align: center;
    // background-color: green;
    border-radius: 0 0 180px 180px;
    overflow: hidden;

    .circular-img-circle {
      height: 360px;
      width: 360px;
      //   background-color: red;
      position: absolute;
      left: 0;
      bottom: 0;
      border-radius: 50%;
    }
  }

  img {
    position: relative;
    z-index: 1;
  }
}
