.page-link,
.page-item.disabled .page-link {
  border: none;
  outline: none;
  background-color: transparent;
  color: var(--black-70);
}

.page-item {
  &.active {
    .page-link {
      background-color: var(--main-color);
    }
  }
}

.page-link {
  &:hover {
    background-color: var(--main-color);
    color: var(--WHITE);
  }

  &:focus {
    box-shadow: none;
    background-color: var(--red-light);
    color: var(--WHITE);
  }

  &:active {
    outline: none;
    border: none;
  }
}
