.nav-tabs {
  .nav-link {
    background-color: var(--white);
    text-transform: capitalize;
    border-color: var(--border-color-2);
    border-radius: 5px;
    padding: 5px 12px;
    margin: 0 5px 10px;
    color: var(--black-70);
    font-size: 16px;

    &.active {
      border-color: transparent;
      background-color: var(--main-color);
      color: var(--WHITE);
    }

    &:hover {
      border-color: var(--border-color-2);
    }
  }
}
