.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1001;
  background-color: var(--white);
  display: flex;

  &.fade-out {
    opacity: 0;
    @include transitionEase(all, 0.6s);
  }

  div {
    margin: auto;
    height: 32px;
    width: 32px;
    border: 4px solid var(--main-color);
    position: relative;
    animation: loader 2s linear infinite;

    &::before {
      content: '';
      position: absolute;
      height: 100%;
      width: 100%;
      background-color: var(--main-color);
      left: 0;
      top: 0;
      animation: loaderIn 2s linear infinite;
    }
  }
}
