// COURSE-DETAILS / FORM SELECT

.form-group {
  margin-bottom: 25px;
  position: relative;
}

.form-control {
  height: 48px;
  border-color: var(--border-color-1);
  color: var(--black-90);
  background-color: transparent;

  &:focus {
    background-color: transparent;
    border-color: var(--border-color-1);
    color: var(--black-90);
    box-shadow: none;
  }
}

.select-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  font-size: 13px;
}

select.form-control:focus {
  option {
    background-color: var(--select-box-bg-color);
  }
}

// LOG IN / FORM
.form-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 30px;
}

.form-control {
  &::placeholder {
    color: var(--black-70);
  }
}

// CONTACT / FORM
textarea.form-control {
  height: 120px;
  resize: none;
}
