.decoration-circles-item {
  position: absolute;
  border-radius: 50%;
  opacity: 0.4;

  &:nth-child(1) {
    height: 30px;
    width: 30px;
    background-color: var(--yellow-light);
    left: 10%;
    top: 20%;
  }

  &:nth-child(2) {
    height: 50px;
    width: 50px;
    background-color: var(--green-light);
    left: 40%;
    top: 40%;
  }

  &:nth-child(3) {
    height: 30px;
    width: 30px;
    background-color: var(--red-light);
    left: 70%;
    top: 70%;
  }

  &:nth-child(4) {
    height: 20px;
    width: 20px;
    background-color: var(--red-light);
    left: 20%;
    top: 50%;
  }
}

.decoration-imgs-item {
  position: absolute;
  border-radius: 50%;
  opacity: 0.4;

  &:nth-child(1) {
    height: 60px;
    width: 60px;
    background-color: var(--yellow-light);
    left: 20%;
    top: 30%;
  }

  &:nth-child(2) {
    height: 80px;
    width: 80px;
    background-color: var(--red-light);
    left: 90%;
    top: 40%;
  }

  &:nth-child(3) {
    height: 40px;
    width: 40px;
    background-color: var(--green-light);
    left: 10%;
    top: 60%;
  }
}
