// COURSE SIDEBAR
.course-sidebar {
  .img-box {
    margin-bottom: 20px;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background-color: var(--black-alpha-40);
    }

    .play-icon {
      position: absolute;
      height: 50px;
      width: 50px;
      left: 50%;
      top: 50%;
      background-color: var(--main-color);
      transform: translate(-50%, -50%);
      font-size: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: var(--WHITE);
    }

    p {
      position: absolute;
      color: var(--WHITE);
      font-weight: 500;
      top: calc(50% + 40px);
      width: 100%;
    }
  }

  .price {
    span {
      margin-right: 8px;
    }

    .price-old {
      text-decoration: line-through;
    }

    .price-new {
      font-size: 30px;
      font-weight: 700;
      color: var(--black-90);
    }

    .price-discount {
      color: var(--main-color);
    }
  }

  .features-list {
    margin-bottom: 20px;

    li {
      position: relative;
      padding-left: 21px;
      margin-bottom: 8px;

      &::before {
        content: '';
        position: absolute;
        height: 6px;
        width: 6px;
        background-color: var(--main-color);
        left: 0;
        top: 9px;
        border-radius: 50%;
      }
    }
  }
}
